// binary convertion functions

function writeLong(value) { // int32
	var buffer = new ArrayBuffer(4);
	var view = new DataView(buffer);
	view.setInt32(0, value, true);
	return new Uint8Array(buffer);
}

function writeInteger(value) { // int16
	var buffer = new ArrayBuffer(2);
	var view = new DataView(buffer);
	view.setInt16(0, value, true);
	return new Uint8Array(buffer);
}

function writeDouble(value) { // float32
	var buffer = new ArrayBuffer(4);
	var view = new DataView(buffer);
	view.setFloat32(0, value, true);
	return new Uint8Array(buffer);
}

function readLong(buffer, offset) { // int32
	let array = new ArrayBuffer(4);
	let view = new DataView(array);
	view.setUint8(0, buffer[offset]);
	view.setUint8(1, buffer[offset + 1]);
	view.setUint8(2, buffer[offset + 2]);
	view.setUint8(3, buffer[offset + 3]);
	let value = view.getInt32(0, true);

	return [value, offset + 4];
}

function readInteger(buffer, offset) { // int16
	let array = new ArrayBuffer(2);
	let view = new DataView(array);
	view.setUint8(0, buffer[offset]);
	view.setUint8(1, buffer[offset + 1]);
	let value = view.getInt16(0, true);

	return [value, offset + 2];
}

function readDouble(buffer, offset) { // float32
	let array = new ArrayBuffer(4);
	let view = new DataView(array);
	view.setUint8(0, buffer[offset]);
	view.setUint8(1, buffer[offset + 1]);
	view.setUint8(2, buffer[offset + 2]);
	view.setUint8(3, buffer[offset + 3]);
	let value = view.getFloat32(0, true);

	return [value, offset + 4];
}

