function stringToUTF8Bytes(string) {
	let utf8 = new Uint8Array(string.length);
	for (let i = 0; i < string.length; i++) {
		let charcode = string.charCodeAt(i);
		utf8[i] = charcode;
	}
	return utf8;
}

export function ReadBinaryIndex(binaryFile) {
	let arrayBuffer = stringToUTF8Bytes(binaryFile);

	console.log("completo ",arrayBuffer);
	console.log("parte 1 ",arrayBuffer.slice(0, 4));
	console.log("parte 2 ",arrayBuffer.slice(4, 8));
	// to string

	console.log(readLong(arrayBuffer, 0));
	console.log(readLong(arrayBuffer, 4));
	
	let index = {
		version: 0,
		maxGrh: 0,
		indexs: [],
	};

	let offset = 0;
	let temp = "";

	[index.version, offset] = readLong(arrayBuffer, offset);
	[index.maxGrh, offset] = readLong(arrayBuffer, offset);

	console.log("Index version:", index.version);
	console.log("Max grh:", index.maxGrh);

	for (let i = 0; i < index.maxGrh; i++) {
		let tempIndex = {
			index: 0,
			frames: [],
			x: 0,
			y: 0,
			w: 0,
			h: 0,
			speed: 0.0,
		};

		[tempIndex.index, offset] = readLong(arrayBuffer, offset);
		let frames = 0;
		[frames, offset] = readInteger(arrayBuffer, offset);

		if (frames == 1) {
			tempIndex.frames.push(readLong(arrayBuffer, offset)[0]);
			offset += 4;
			[tempIndex.x, offset] = readInteger(arrayBuffer, offset);
			[tempIndex.y, offset] = readInteger(arrayBuffer, offset);
			[tempIndex.w, offset] = readInteger(arrayBuffer, offset);
			[tempIndex.h, offset] = readInteger(arrayBuffer, offset);
		} else if (frames > 1) {
			for (let j = 0; j < frames; j++) {
				tempIndex.frames.push(readLong(arrayBuffer, offset)[0]);
				offset += 4;
			}
			[tempIndex.speed, offset] = readDouble(arrayBuffer, offset);
		}

		if (tempIndex.index > index.maxGrh) {
			console.log("Grh index ", tempIndex.index, " is greater than max grh");
			return index;
		}

		if (tempIndex.index >0) {
			console.log(tempIndex);
			index.indexs.push(tempIndex);
		}
	}

	return index;
}


export function CompileBinaryIndex(index) {
    var binaryFile = [];

	binaryFile.push(writeLong(index.version));
	binaryFile.push(writeLong(index.maxGrh));

	for (var i = 0; i < index.indexs.length; i++) {
		binaryFile.push(writeLong(index.indexs[i].grhIndex));
		binaryFile.push(writeInteger(index.indexs[i].frames.length));

		if (index.indexs[i].frames.length == 1) {
			binaryFile.push(writeLong(index.indexs[i].frames[0]));
			binaryFile.push(writeInteger(index.indexs[i].posX));
			binaryFile.push(writeInteger(index.indexs[i].posY));
			binaryFile.push(writeInteger(index.indexs[i].width));
			binaryFile.push(writeInteger(index.indexs[i].height));
		} else if (index.indexs[i].frames.length > 1) {
			for (var j = 0; j < index.indexs[i].frames.length; j++) {
				binaryFile.push(writeLong(index.indexs[i].frames[j]));
			}
			binaryFile.push(writeDouble(index.indexs[i].speed));
		}
	}

	return binaryFile;
}