<script>
  import { flagIndexLoaded, indexsComplete, resourcesComplete } from '../stores.js';
  import { AddGraphic, LoadGraphics } from '../indexdb.js';

  import { ReadBinaryIndex } from './logic/indexCompiler.js';

  import {
    Button,
    Modal,
    ModalBody,
    ModalFooter,
    ModalHeader,
    Row,
    Col,
    InputGroup,
    InputGroupText,
    Input,
    Spinner,
    Alert,
    NavLink,
  } from 'sveltestrap';

  export let mode;

  let open = false;
  let uploading = false;
  let msgError = "";
  let msgResources = "";

  const toggle = () => {
    if (uploading) {
      return;
    }
    msgResources = "";
    msgError = "";
    open = !open;
  }

  function upload() {
    if (uploading) {
      return;
    }

    if (document.getElementById('fileResources').files.length > 0) {

      if (!indexedDB) {
        alert("Tu navegador no soporta IndexedDB");
        return;
      }
      
      let count = 0;
      let loading = 0;
      msgResources = "Cargando recursos...";
      msgError = "";
      //let resourcesCompleteTemp = [];
      for (let i = 0; i < document.getElementById('fileResources').files.length; i++) {
        let file = document.getElementById("fileResources").files[i];

        if (file) {
          let grhNum = file.name.split('.')[0];

          if (file.webkitRelativePath) {
            let pathString = file.webkitRelativePath;
            pathString = pathString.replace(/\\/g, "/")
            pathString = pathString.substring(0,pathString.lastIndexOf("/"+file.name));
            let path = pathString.split('/');
            if (path.length > 1) {
              console.log("GRH ignored by subfolder: " + file.name + " (" + path[0] + ")" + " (" + path[1] + ")");
              continue;
            }
          }
          if (isNaN(grhNum)) {
            console.log("Invalid GRH number: " + file.name);
            continue;
          }

          let body = new Blob;

          var reader = new FileReader();
          reader.onload = function (evt) {
            body = evt.target.result;
            console.log("GRH added: " + file.name);
            // console.log(file);
            AddGraphic(grhNum, body);
            count++;
            msgResources = count + "/" + loading + " Recursos cargados! 🦎";
            if (count == loading) {
              msgResources = "Inicializando recursos! 🦎";
              msgError = "";
              LoadGraphics();
            }
          };

          reader.onerror = function (evt) {
            msgError = evt;
            return;
          }

          loading++;
          reader.readAsBinaryString(file);
          
        }

      }
      if (msgError != "") {
        return;
      }
    }

    if (document.getElementById('fileIndexs').files.length > 0) {
      if (document.getElementById('fileIndexs').files[0].size > 5000000) {
        msgError = "El archivo seleccionado es muy grande";
        return;
      }
      msgError = "";
      uploading = true;

      function receivedBinary() {
        uploading = false;
        msgResources = "Indice cargado! 🦎";
        toggle();
        let data = ReadBinaryIndex(fr.result);
        $indexsComplete = data;
        return;
      }

      let fr = new FileReader();
      fr.onload = receivedBinary;
      fr.readAsBinaryString(document.getElementById('fileIndexs').files[0]);

    }

    //   let url = apiServer + 'index/read';
    //   fetch(url, {
    //     method: 'POST',
    //     body: formData,
    //   })
    //     .then(response => response.json())
    //     .then(data => {
    //       console.log(data);
    //       uploading = false;
    //       toggle();
    //       $indexsComplete = data;
    //     })
    //     .catch(error => {
    //       console.log(error);
    //       msgError = error;
    //       uploading = false;
    //     });
    // };

    if (msgError.length == 0) {
      $flagIndexLoaded = true;
    };
  }

</script>
  
<div>
  {#if (mode=='button')}
    <Button color="primary" on:click={toggle} title="Subir un nuevo indice o recursos graficos">Cargar recursos</Button>
  {:else}
    <NavLink color="warning" on:click={toggle} title="Subir un nuevo indice o recursos graficos">Cargar recursos</NavLink>
  {/if}
  <Modal isOpen={open} {toggle} modalClassName="modal-dark">
    <ModalHeader {toggle}>Cargar recursos</ModalHeader>
    <ModalBody>
      <h6>Selecciona la carpeta en donde estan tus graficos BMP o PNG.</h6>
      <Row>
        <Col>
          <InputGroup class="input-dark">
            <Input type="file" name="fileResources" id="fileResources" webkitdirectory directory multiple />
            <!-- webkitdirectory directory  -->
            <InputGroupText>
              Recursos
            </InputGroupText>
          </InputGroup>
        </Col>
      </Row>
      <br/>
      <h6>Selecciona el indice .ind compilado.</h6>
      <Row>
        <Col>
          <InputGroup class="input-dark">
            
            <Input type="file" name="fileIndexs" id="fileIndexs" />
            <InputGroupText>
              Indices de Graficos
            </InputGroupText>
          </InputGroup>
        </Col>
      </Row>
      <!-- <Row>
        <Col>
          <InputGroup>
            <InputGroupText>
              Indices de Cabezas
            </InputGroupText>
            <Input type="file" name="head" id="head" />
          </InputGroup>
        </Col>
      </Row> -->
    </ModalBody>
    <ModalFooter>
      {#if (msgResources)}
        <Row><Alert color="success">{msgResources}</Alert></Row>
      {/if}
      {#if (msgError)}
        <Row><Alert color="danger">{msgError}</Alert></Row>
      {/if}
      {#if !uploading} 
        <Row>
          <Col><Button color="primary" on:click={upload}>Cargar</Button></Col>
          <Col><Button color="secondary" on:click={toggle}>Cancelar</Button></Col>
        </Row>
      {:else}
        <Spinner primary  />
      {/if}
    </ModalFooter>
  </Modal>
</div>