<script>
	import { onMount, onDestroy } from "svelte";
	import { Row, Col, Badge, Button, Alert, Image, ListGroup, ListGroupItem,
		Spinner, InputGroup, Input, InputGroupText, 
		Pagination, PaginationItem, PaginationLink, FormGroup } from 'sveltestrap';

	import { indexsComplete, resourcesComplete, indexSelected, events} from '../../stores.js';
	import { GetResourceBinary } from '../../indexdb.js';

	import ModalLoadResources from '../modalLoadResources.svelte';
	import ModalExportIndex from './modalExportIndex.svelte';

	const maxIndexPerPage = 100;
	let page = 1;
	let maxPages = 1;
	let loaded = false;
	let editingPage = false;
	let pageValue = 1;

	let fixedPreview = false;
	let isSaveOK = false;
	let clicking = false;
	let _indexSelected = 0;
	let filterIndex = '';
	let maxGrh = 0;
	let version = 0;
	let indexs = [];

	function findIndexOnPage(num) {
		let count = 0;
		let pageFound = 0;
		let indexFound = undefined;

		for (let i = 0; i < $indexsComplete.indexs.length; i++) {
			count++;
			if (count>maxIndexPerPage) {
				pageFound++;
				count = 1;
			};
			let item = $indexsComplete.indexs[i];
			if (item.index >= num) {
				if (item.index == num) {
					indexFound = item;
				}
				break;
			}
		};

		if (pageFound>=0) {
			movePage(pageFound);
			if (indexFound!=undefined) {
				selectIndex(indexFound);
			}
		}
	}

	function newIndex(type) {
		let created = false;
		if (type==1) { // al primer slot vacio
			console.log("al primer slot vacio");
			let lastIndex = 0;
			for (let i = 0; i < $indexsComplete.indexs.length; i++) {
				if ($indexsComplete.indexs[i].index != lastIndex+1) {
					let newNum = lastIndex+1;
					let newIndex = [];

					for (let t = 0; t < $indexsComplete.indexs.length; t++) {
						if (t == i) {
							newIndex.push({index: newNum, frames:[0], x: 0, y: 0, w: 0, h: 0, speed: 0});	
						}
						newIndex.push($indexsComplete.indexs[t]);
					}
					created = true;
					$indexsComplete.indexs = newIndex;
					findIndexOnPage(newNum);

					break;
				} else {
					lastIndex = $indexsComplete.indexs[i].index;
				}
			}
		}
		if (type==0 || !created) { // al final
			let newNum = $indexsComplete.max_grh + 1;
			$indexsComplete.indexs.push({index: newNum, frames:[0], x: 0, y: 0, w: 0, h: 0, speed: 0});
			$indexsComplete.max_grh = newNum;
			findIndexOnPage(newNum);
		} 
	}

	function saveIndex() {
		let body = $indexsComplete;
		window.localStorage.setObject ("indexsComplete", body);
		isSaveOK = true;
		setTimeout(() => {
			isSaveOK = false;
		}, 2000);
	}

	function editPage() {
		if (editingPage==true) {
			if (pageValue>0 && pageValue<=maxPages) {
				changePage(pageValue);
			}
			pageValue = page;
		}
		editingPage = !editingPage;
	}

	function movePage(_move) {
		if (_move == 0 || page + _move < 1 || page + _move > maxPages || loaded == false) {
			return;
		}
		changePage(page + _move);
	}

	function changePage(_page) {
		if (_page <= 0 || _page > maxPages || _page == page || loaded == false) {
			return;
		}
		showPage(_page);
	}

	function showPage(_page) {

		if ($indexsComplete == null ||
			$indexsComplete.indexs == null) {
			return;
		}

		page = _page;
		pageValue = page;

		indexs = [];
		let count = 0;
		let start = (page - 1) * maxIndexPerPage;

		$indexsComplete.indexs.forEach(index => {
			count++;
			if (count>start) {
				if (indexs.length>=maxIndexPerPage) {
					return;
				}
				let add = true;
				if (filterIndex!=null && String(filterIndex).length>0) {
					if ((String(index.index).indexOf(filterIndex)==-1) 
						&& (index.frames.length > 0 && String(index.frames[0]).indexOf(filterIndex)==-1)) {
						add = false;
					}
				}
				if (add) {
					indexs.push(index);
				}
			}
		});

	}

	function getIndexs() {
		loaded = false;

		if ($indexsComplete == null ||
			$indexsComplete.indexs == null) {
			return;
		}

		maxGrh = $indexsComplete.max_grh;
		version = $indexsComplete.version;

		maxPages = Math.ceil(($indexsComplete.indexs).length / maxIndexPerPage);

		showPage(1);

		loaded = true;
	}

	function findFilter() {
		page = 1;
		getIndexs();
	}

	function selectIndex(index) {
		_indexSelected = index;
		clicking = true;
		indexSelected.set(index);
	}

	async function showGrh(numInd, first) {

		const result = new Promise((resolve, reject) => {

			if ($indexsComplete.indexs == undefined) {
				reject();
			}

			if (indexs.length>0) {
				let ret = "";
				$indexsComplete.indexs.forEach(e => {
					if (e.index == numInd) {
						// console.log("showGrh index encontrado", e);
						if (e.frames.length == 1 && e.w > 0 && e.h > 0) {
							GetResourceBinary(e.frames[0]).then(function(img) {						
								let image = "background-image: url("+ img +");";
								// let image = "background-image: url(" + apiServer + `graphics/` + grhToHash(e.frames[0]) + ");";
								let position = "background-position: -" + e.x + "px -" + e.y + "px;";
								ret = `<img style="${image}${position}width:${e.w}px;height:${e.h}px;background-color: black;">`;
								resolve(ret);
							});
						} else { // Es animación!
							if(first == false) {
								ret = "";
							}
							$indexsComplete.indexs.forEach(y => {
								if (y.index == e.frames[0]) {
									GetResourceBinary(y.frames[0]).then(function(img) {						
										let image = "background-image: url("+ img +");";
										let position = "background-position: -" + y.x + "px -" + y.y + "px;";
										ret = `<img style="${image}${position}width:${y.w}px;height:${y.h}px;background-color: black;">`;
										resolve(ret);
									});
								};
							});
						}					
					}
				});
			};
		});

		return await result;
	}

	let unsubscribeComplete = indexsComplete.subscribe(_ => {
		getIndexs();
	});

	let unsubscribe = indexSelected.subscribe(_ => {

		if ($indexSelected == undefined ||
			$indexSelected == null || 
			$indexSelected.index == undefined ||
			clicking == true) {
			clicking = false;
			return;
		}

		const ind = $indexSelected;

		for (let i = 0; i < indexs.length; i++) {
			if (indexs[i].index == ind.index) {
					indexs[i] = ind;
				break;
			}
		}
        //showPage(page);

    });

	let unsubscribeEvents = events.subscribe(state => {
		if (state == null || state== undefined) {
			return;
		}
		if (state.action == "deleteIndex") {
			findIndexOnPage(state.index);
		};
	});

	onMount(async () => {
		getIndexs();
	});

	onDestroy(async () => {
        unsubscribe();
		unsubscribeComplete();
		unsubscribeEvents();
    });

</script>

<Col class="input-dark">
	<Row cols={{ md: 2, sm: 1 }} noGutters>
		<Col>
			<InputGroup>
				<Button color="primary" style="width: inherit;" on:click="{() => saveIndex()}" disabled={isSaveOK} >Guardar indice en el navegador</Button>
			</InputGroup>
		</Col>
		<Col>
			<InputGroup>
				<ModalExportIndex />
			</InputGroup>
		</Col>
	</Row>
	<InputGroup>
		<Input bind:value={filterIndex} on:keyup={findFilter} type="number" placeholder="Número de Index" />
	</InputGroup>
	{#if (loaded == false)}
		<Row>
			<Col class="text-center" style="padding: 2em; color: white;">
				<div style="padding-bottom: 1em;">Se requiere un indice válido. Puedes crear uno nuevo o importar uno existente.
					<ModalLoadResources mode="button" />
				</div>
				<Spinner primary  />
			</Col>
		</Row>
	{:else}
		<InputGroup>
			<InputGroupText>MaxGrh</InputGroupText>
			<Input bind:value={maxGrh} type="number" placeholder="MaxGrh" />
		</InputGroup>
		<InputGroup>
			<InputGroupText>Version</InputGroupText>
			<Input bind:value={version} type="number" placeholder="Version" />
		</InputGroup>
		<InputGroup>
			<Pagination style="position: relative; flex: 1 1 auto;">
				<PaginationItem>
					<PaginationLink first href="#" on:click="{() => changePage(1)}" />
				</PaginationItem>
				<PaginationItem>
					<PaginationLink previous href="#" on:click="{() => movePage(-1)}" />
				</PaginationItem>
				<PaginationItem style="position: relative; flex: 1 1 auto;">
					{#if !editingPage }
					<PaginationLink href="#" style="position: relative; flex: 1 1 auto; text-align: center;"
						on:click="{() => editPage()}">{page}</PaginationLink>
					{:else}
						<Input bind:value={pageValue} type="number" placeholder="Página" 
							on:change="{() => editPage()}" on:blur="{() => editPage()}" />
					{/if}
				</PaginationItem>
				<PaginationItem>
					<PaginationLink next href="#" on:click="{() => movePage(1)}" />
				</PaginationItem>
				<PaginationItem>
					<PaginationLink last href="#" on:click="{() => changePage(maxPages)}" />
				</PaginationItem>
			</Pagination>
		</InputGroup>

		<FormGroup>
			<Input on:change="{() => fixedPreview = !fixedPreview}" type="checkbox" label="Vista previa de tamaño fijo" />
		</FormGroup>

		<ListGroup style="overflow: auto; height: 820px; min-width:240px;">
		{#if indexs != undefined && indexs != []}
			{#each indexs as index}
				<ListGroupItem style="display: inline-flex; vertical-align: top; min-width: 50px;" class="{fixedPreview ? 'fixedBox': ''}" tag="button" active={_indexSelected == index} action on:click="{selectIndex(index)}">
					<div style="display: inline-flex; vertical-align: top; min-width: 50px;">
						{index.index} {#if index.frames.length > 1}<Badge style="position: absolute; right: 1em;">Anim</Badge>{/if}
					</div>
					{#if fixedPreview}
						<div style="max-height: 50px; max-width: 50px;">
							{#await showGrh(index.index, true) then grh}
								{@html grh}
							{/await}
						</div>
					{:else}
						{#await showGrh(index.index, true) then grh}
							{@html grh}
						{/await}
					{/if}
				</ListGroupItem>
			{/each}
		{/if}
		</ListGroup>

		<Row cols={{ md: 2, sm: 1 }} noGutters>
			<Col>
				<InputGroup>
					<Button color="success" style="width: inherit;" on:click="{() => newIndex(1)}" >Nuevo indice en el primer slot disponible</Button>
				</InputGroup>
			</Col>
			<Col>
				<InputGroup>
					<Button color="success" style="width: inherit;" on:click="{() => newIndex(0)}" >Nuevo indice al final</Button>
				</InputGroup>
			</Col>
		</Row>
	{/if}
</Col>

<Alert color="primary" isOpen={isSaveOK} style="position: absolute;right:2em;bottom:2em;z-index: 10;opacity: 0.9;">Indice guardado correctamente.</Alert>
